import styled from "styled-components";

/*
 * Animation for revealing a section when scrolled onto.. CSS (1st phase) Setup our CSS
 */

const Revealable = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-left: ${({ show }) => (show ? "0" : "100px")};
  transition: opacity ease-in 2s, margin-left cubic-bezier(1, 0, 0.4, 1) 2s;
`;

const RevealableRight = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-right: ${({ show }) => (show ? "0" : "100px")};
  transition: opacity ease-in 2s, margin-right cubic-bezier(1, 0, 0.4, 1) 2s;
`;

export { RevealableRight };

export default Revealable;
