import React, { Component } from "react";
import { Waypoint } from "react-waypoint";
import Revealable, { RevealableRight } from "../Components/Revealable";

/*
 * Animation for revealing a section when scrolled onto.. using Waypoint (2nd phase)
 */

class Reveal extends Component {
  state = {
    revealed: false
  };
  reveal = () => {
    this.setState({ revealed: true });
  };
  unreveal = () => {
    this.setState({ revealed: false });
  };

  render() {
    return (
      <Waypoint onEnter={this.reveal} onLeave={this.unreveal}>
        <Revealable show={this.state.revealed}>
          {this.props.children}
        </Revealable>
      </Waypoint>
    );
  }
}

export default Reveal;

class RevealRight extends Component {
  state = {
    revealed: false
  };
  reveal = () => {
    this.setState({ revealed: true });
  };
  unreveal = () => {
    this.setState({ revealed: false });
  };

  render() {
    return (
      <Waypoint onEnter={this.reveal} onLeave={this.unreveal}>
        <RevealableRight show={this.state.revealed}>
          {this.props.children}
        </RevealableRight>
      </Waypoint>
    );
  }
}
export { RevealRight };
