import React, { Component } from "react";

class Navbar extends Component {
  constructor() {
    super();
    this.state = {
      width: window.innerWidth,
      isTrue: false
    };
  }

  componentDidMount() {
    this.resized();
  }

  componentWillUnmount() {
    this.resized();
  }

  resized = () => {
    let inner_width = window.innerWidth;
    if (inner_width >= 720) {
      this.setState({
        isTrue: true
      });
    } else {
      this.setState({
        isTrue: false
      });
    }
    /*370px is phone width */
    console.log(this.state.isTrue + " :" + inner_width);
  };

  render() {
    return (
      <div className="nav_container center">
        <nav
          className="nav"
          style={{
            backgroundColor: "tomato",
            position: this.props.links.position,
            marginTop: this.props.links.padding
          }}
        >
          <a href="#d">
            <h1>Anthony Hsia</h1>
          </a>
          <div className="div_links">
            <ul className="nav_links">
              <a href="#a">
                <li>About Me</li>
              </a>

              <a href="#b">
                <li>Projects</li>
              </a>

              <a href="#c">
                <li>Contact </li>
              </a>
            </ul>
          </div>
        </nav>
      </div>
    );
  }
}

export default Navbar;
